
.CompanyHeadline {
  font-size: 50px;
  font-family: "inter-Bold";
  color:white;
  margin-left:50px;
  margin-bottom:20px;
letter-spacing: -1px;
}

.Aktiendashboard-CompanyInfo-container{
    height: 100%;
    margin-bottom: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
  }


/* Ab hier FirstRow */

.CompanyFirstRow {
  display:inline-flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom:100px;
}



/* erste Tabelle */

.CompanyTableFirstRowContainer{
  display: flex;
  flex-direction: column;
  font-size: 16px;
  background: linear-gradient(20deg, rgba(255, 255, 255, 0.20), rgba(255,255,255,10%),  rgba(255,255,255,0%));
  border: solid;
  border-color: white;
  border-radius: 14px;
  border: 0.25px;
  border-style: outset;
  border-color: #ffffff;
  padding: 20px;
  cursor: default;
  transition: 0.3s ease;
  box-shadow: 0 70px 63px -60px #000;

  margin-left:auto;
  margin-right:auto;

}

.CompanyTableFirstRowContainer:hover{
  transform: scale(1.02);
}


table.CompanyTableFirstRow {
  table-layout: fixed;
  color:white;
  width: 100%;
  height: 220px;
  text-align: left;
  border-collapse: collapse;
}

table.CompanyTableFirstRow td, table.CompanyTableFirstRow th {

  padding: 15px 0px;
}

table.CompanyTableFirstRow tbody td {
  font-size: 14px;
  color: white;
}

table.CompanyTableFirstRow tr:nth-child(even) {
  background: rgba(255,255,255,0.0);
}

table.CompanyTableFirstRow  td:nth-child(2) {
  text-align: right;
}

tr:hover, table.CompanyTableFirstRow tr:nth-child(even):hover
 {background-color: rgba(0,0,0,0.1);
  transition: 0.3s ease;}


/* Ab hier SecondRow */

.CompanySecondRow{
  display:flex;
  justify-content: space-between;
  align-items:center;
  flex-wrap: wrap;
  margin-top:20px;
  gap: 50px;
}

@media screen and (max-width: 700px) {
  .CompanySecondRow{
    display:block;
    justify-content: space-between;
    align-items:center;
    flex-wrap: wrap;
  }
}

.CompanyTableSecondRowContainer{
  display: flex;
  flex-direction: column;
  font-size: 16px;
  /* background: linear-gradient(20deg, rgba(255, 255, 255, 0.20), rgba(255,255,255,10%),  rgba(255,255,255,0%)); */
  border: solid;
  border-color: white;
  border-radius: 14px;
  border: 0.25px;
  border-style: outset;
  border-color: #ffffff;
  padding: 20px;
  cursor: default;
  box-shadow: 0 70px 63px -60px #000;
  flex-wrap: wrap;
  color: white;
  margin-bottom: 20px;
}

@media screen and (max-width: 1500px) {
  .CompanyTableSecondRowContainer{
  width: 100%;
  }
}


@media screen and (max-width: 700px) {
  .CompanyTableSecondRowContainer{
    display: flex;
    flex-direction: column;
    font-size: 16px;
    background: none;
    border: none;
    border-color: none;
    border-style: none;
    border-color: none;
    cursor: default;
    flex-wrap: wrap;
    width: 95%;
    margin: auto;
    box-shadow: 0 70px 63px -60px rgba(255,255,255,0);
    margin-bottom: 0px;
    padding: 0px;
    transition: 0s ease;
  }

  .CompanyTableSecondRowContainer:hover{
    transform: none;
  }


  tr {
    border-bottom: 1px solid rgba(255,255,255,0.6);
}

tr:hover, table.CompanyTableSecondRow tr:nth-child(even):hover
{background-color: rgba(0,0,0,0);
  transition: 0.3s ease;}

}


